#background {
    width: 100vw;
    height: 100vh;
    background: tomato;
    overflow: hidden;
    position: fixed;
  }
  
  #background::before {
    content: "";
    display: block;
    width: 200vw;
    height: 200vw;
    border-radius: 50%;
    background: white;
    position: absolute;
    bottom: 50vh;
    left: 50%;
    transform: translateX(-50%);
  }
  
  #wrapper {
    position: relative;
    color: #fff;
    text-align: center;
  }